import React, { Component } from 'react';
import {BrowserRouter,Route,Link} from 'react-router-dom';
import Profile from './Profile';
import data from './data.json'
import icon from './perimg.svg'
let Resume1=(props)=>
{
    let info=Object.values(props.location.index.value);
    let person=data.profiles[info];
    console.log(person)

    return(
        <div>
            <div className="parent" >
                <div className="child1">
                <img src={icon} alt="profile" />
                    <h3>{person.basics.name}</h3>
                    <h3>{person.basics.email}</h3>
                    <h3>{person.basics.mobile}</h3>
                </div>
                <div className="child2">
                <u> <h1>Eductional Qualifications</h1></u>
                    {
                        person.education.map((i,j)=>(
                            <div key={j}>
                                <h4>{i.degree}</h4>
                                <ul>
                                    <li>{i.percentage}</li>
                                    <li>{i.institute}</li>
                                </ul>
                            </div>
                        ))
                    }
                    <hr></hr>
                    <h3>Skills</h3> <hr></hr>
                    {
                        person.skills.map((p,q)=>(
                            <div key={q}>
                                <h3>{p.type}</h3>
                                {
                                    p.values.map((r,s)=>(
                                        <div key={s} style={{display:"inline"}}>
                                            <span className="skills">{r}
                                            </span>
                                        </div>
                                    ))
                                }
                            </div>
                        ))
                    }
                <div >

            </div>
            </div>
            <br></br>
            </div>
            <br></br><br></br>
            <div className="btn">
                <Link to="/"  className="back">Back</Link>
            </div>
        </div>
    )
}

export default Resume1;